<template>
  <div></div>
</template>

<script>
import { Store } from "@/stores";

const store = Store();
export default {
  name: "HomeView",
  setup() {
    return {};
  },
  mounted() {
    try {
      if (!localStorage.getItem("jshtcm")) {
        this.$router.push("/login");
      } else {
        const jshtcm = localStorage.getItem("jshtcm") || "";
        store.token = JSON.parse(jshtcm).token;
        store.accessToken = JSON.parse(jshtcm).accessToken;
        this.$router.push("/hospitalzone");
      }
    } catch (e) {
      this.$router.push("/login");
    }
  },
};
</script>

